<template>
<div id="contract-option">
    <div class="row">
        <div class="col-10 offset-1">
            <div class="card">
                <div class="card-body">
                    <h1 class="fw-bold">Tùy chỉnh hợp đồng</h1>

                    <div class="row">
                        <div class="col-6">
                            <div class="form-group">
                                <label for="">Tên công ty</label>
                                <input v-model="contract.company_name" type="text" placeholder="Tên công ty" class="form-control" />
                            </div>
                        </div>
                        <div class="col-3">
                            <div class="form-group">
                                <label for="">Mã số thuế</label>
                                <input type="text" placeholder="Mã số thuế công ty" v-model="contract.company_taxcode" class="form-control" />
                            </div>
                        </div>
                        <div class="col-3">
                            <div class="form-group">
                                <label for="">Ngày thành lập</label>
                                <date-picker v-model="contract.company_date_create" :config="option"></date-picker>
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-12">
                            <div class="form-group">
                                <label for="">Địa chỉ</label>
                                <input type="text" v-model="contract.company_address" class="form-control" placeholder="Địa chỉ công ty" />
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Cơ quan thuế quản lý</label>
                                <input v-model="contract.company_tax_manager" type="text" placeholder="Cơ quan thuế quản lý" class="form-control" />
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Ngành nghề kinh doanh</label>
                                <input v-model="contract.company_bussiness_type" type="text" placeholder="Ngành nghề kinh doanh" class="form-control" />
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Nơi cấp mã số thuế</label>
                                <input v-model="contract.company_taxcode_issuedby" type="text" placeholder="Nơi cấp mã số thuế" class="form-control" />
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Người đại diện</label>
                                <input type="text" v-model="contract.owner_name" class="form-control" placeholder="Người đại diện" />
                            </div>
                        </div>
                        <div class="col-3">
                            <div class="form-group">
                                <label for="">Chức vụ</label>
                                <input type="text" v-model="contract.owner_position" class="form-control" placeholder="Chức vụ" />
                            </div>
                        </div>
                        <div class="col-2">
                            <div class="form-group">
                                <label for="">Giới tính</label>
                                <input type="text" v-model="contract.owner_sex" class="form-control" placeholder="Giới tính" />
                            </div>
                        </div>

                        <div class="col-3">
                            <div class="form-group">
                                <label for="">Ngày sinh</label>
                                <date-picker v-model="contract.owner_birthday" :config="option"></date-picker>
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-4">
                            <div class="form-group">
                                <label>Số CCCD/CMND:</label>
                                <input type="text" v-model="contract.owner_card_id" class="form-control">
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="form-group">
                                <label>Ngày cấp CCCD/CMND:</label>
                                <date-picker v-model="contract.owner_card_date" :config="option"></date-picker>
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="form-group">
                                <label>Nơi cấp CCCD/CMND:</label>
                                <input type="text" v-model="contract.owner_card_issued" class="form-control" />
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-12">
                            <div class="form-group">
                                <label for="">Giấy ủy quyền số:</label>
                                <input type="text" v-model="contract.authority_code" placeholder="Giấy ủy quyền số" class="form-control" />
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Số điện thoại</label>
                                <input type="text" placeholder="Số điện thoại" v-model="contract.company_phone" class="form-control" />
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Email</label>
                                <input type="text" placeholder="Email công ty" v-model="contract.company_email" class="form-control" />
                            </div>
                        </div>

                    </div>

                    <div class="row">
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Tài khoản</label>
                                <input type="text" placeholder="Tài khoản" v-model="contract.company_bankcode" class="form-control" />
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Mở tại</label>
                                <input type="text" placeholder="Mở tại" v-model="contract.company_bankname" class="form-control" />
                            </div>
                        </div>

                    </div>
                   
                    <h1 class="mt-2 fw-bold">Thông tin hợp đồng</h1>
                    <div class="row">
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Số hợp đồng</label>
                                <input type="text" v-model="contract.contract_number" placeholder="Số hợp đồng" class="form-control" />
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Ngày hợp đồng</label>
                                <date-picker v-model="contract.contract_date" :config="option"></date-picker>
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="form-group">
                                <label for="">Số tháng đăng ký</label>
                                <input type="text" v-model="contract.contract_expired_month" placeholder="Số tháng đăng ký" class="form-control" />
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-6">
                            <div class="form-group">
                                <label for="">Ngày bắt đầu</label>
                                <date-picker v-model="contract.contract_date_start" :config="option"></date-picker>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="form-group">
                                <label for="">Ngày kết thúc</label>
                                <date-picker v-model="contract.contract_date_end" :config="option"></date-picker>
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-6">
                            <div class="form-group">
                                <label for="">Mã BHXH/Số lượng hóa đơn</label>
                                <input type="text" v-model="contract.contract_extra1" placeholder="Mã BHXH/Số lượng hóa đơn" class="form-control" />
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="form-group">
                                <label for="">Ký hiệu/Mẫu số</label>
                                <input type="text" v-model="contract.contract_extra2" placeholder="Ký hiệu/Mẫu số" class="form-control" />

                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-12">
                            <table class="table table-responsive mt-2">
                                <thead class="table-dark">
                                    <tr>
                                        <th class="rounded-start">#</th>
                                        <th>Dịch vụ</th>
                                        <th>Số lượng</th>
                                        <th>Đơn giá</th>
                                        <th class="rounded-end">Thành tiền</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(item, index) in contract.details" :key="item.contract_value_detail_id">
                                        <td>{{ index + 1 }}</td>
                                        <td>{{ item.detail_content }}</td>
                                        <td>{{ item.quantity }}</td>
                                        <td>{{ item.price | formatamout(0) }}</td>
                                        <td>{{ item.total| formatamout(0) }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>

                <div class="card-footer">
                    <button class="btn btn-primary" @click="store()">
                        <i class="fa-light fa-floppy-disk"></i> Lưu
                    </button>
                    <router-link class="btn btn-secondary ms-1" :to="{
                name: 'contract-index',
              }"><i class="fa-light fa-angle-left"></i> Quay lại</router-link>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import axios from "axios";
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";

export default {
    components: {
        datePicker,
    },
    data() {
        return {
            contract: {},
            contract_id: 0,
            option: {
                format: "DD/MM/YYYY",
                useCurrent: false,
            },
        };
    },
    created() {
        let id = this.$route.params.id;
        this.getContractValue(id);
    },
    methods: {
        async store() {
            this.$econtract.showLoading();
            await axios.put("contractoption/update", this.contract).then((response) => {
                // if (response.data.type == 'success' && response.status == 200) {
                //     this.$toast.success('Cập nhật hợp đồng thành công');
                // }
                this.$toast.success('Cập nhật hợp đồng thành công');
                this.$router.push({
                    name: "contract-index",
                });
                this.$econtract.hideLoading();
            });
        },
        async getContractValue(id) {
            this.$econtract.showLoading();
            await axios.get("contractoption/" + id).then((response) => {
                if (response.status == 200) {
                    if (response.data.error != "Not found") {
                        this.contract = response.data;
                        this.contract.owner_birthday = this.$econtract.formatDate(
                            response.data.owner_birthday
                        );
                        this.contract.contract_date = this.$econtract.formatDate(
                            response.data.contract_date
                        );
                        this.contract.contract_date_start = this.$econtract.formatDate(
                            response.data.contract_date_start
                        );
                        this.contract.contract_date_end = this.$econtract.formatDate(
                            response.data.contract_date_end
                        );
                        this.contract.owner_card_date = this.$econtract.formatDate(
                            response.data.owner_card_date
                        );
                        this.contract.company_date_create = this.$econtract.formatDate(
                            response.data.company_date_create
                        );
                    } else {
                        this.$toast.warning("Không tìm thấy thông tin");
                        this.$router.push({
                            name: "contract-index",
                        });
                    }
                }

                this.$econtract.hideLoading();
            });
        },
    },
};
</script>
